.header__container {
    width: 320px;
    padding: 44px 20px 92px;
    margin-right: auto;
    margin-left: auto;
    background-image: url("../images/320/abstract-background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    @media screen and (min-width: 768px) {
                width: 768px;
                padding: 40px 32px 81px;
                background-image: url("../images/768/abstract-background.jpg");
            }
                        @media screen and (min-width: 1280px) {
                                    width: 1280px;
                                    background-image: url("../images/1280/abstract-background.jpg");
                                }
                            }

.header__link-box {
    display: flex;
    align-items: center;
}

.logo {
    margin-right: 112px;
    @media screen and (min-width: 768px) {
        display: flex;
            align-items: center;
            margin-right: 387px;
    }
        @media screen and (min-width: 1280px) {
            margin-right: 898px;
        }
}

.logo__icon {
    width: 24px;
    height: 24px;
}

.logo__text {
    display: none;
    @media screen and (min-width: 768px) {
        font-weight: 500;
            font-size: 30px;
            line-height: 1.16;
            display: flex;
            align-items: center;
            text-align: center;
        
            color: #FFFFFF;
            margin-left: 10px;
    }
}

.header-list {
    display: flex;
}

.header-list__item {
&:not(:last-child) {
    margin-right: 40px;
}}

.header-list__link {
    font-weight: 500;
    font-size: 12px;
    line-height: 1.66;
    text-transform: uppercase;
    color: #FFFFFF;
    position: relative;
    &--active {
        &::after {
                content: '';
                position: absolute;
                left: 0;
                background-color: #FF001B;
                height: 3px;
                width: 100%;
                bottom: -3px;
            }
    }
}

.header-form {
    border-bottom: 0.5px solid #FFFFFF;
    width: 280px;
    margin-top: 60px;
    @media screen and (min-width: 768px) {
        margin-right: auto;
            margin-left: auto;
            margin-top: 40px;
            width: 336px;
    }
        @media screen and (min-width: 1280px) {
            width: 394px;
        }
}

.header-form__input {
    border: none;
    background-color: transparent;
    width: 240px;
    outline: none;
    color: #FFFFFF;
    &::placeholder {
            font-weight: 400;
            font-size: 14px;
            line-height: 1.14;
            display: flex;
            align-items: center;
            color: #FFFFFF;
        }
    @media screen and (min-width: 768px) {
        width: 300px;
    }
        @media screen and (min-width: 1280px) {
            width: 360px;
        }
}

.header-form__btn {
    border: none;
    background-color: transparent;
}

.header-form__btn-icon {
    width: 12px;
    height: 12px;
}